
@mixin _page-title(){
  height: 460px;
  text-align: center;
  position: relative;
  font-family: 'Barlow Condensed', sans-serif;
  &__txtarea{
    width: 100%;
    position: absolute;
    top:50%;
  }
  &__title{
    font-size: 7.2rem;
    font-weight: 500;
    line-height: 1;
    letter-spacing: 0.75em;
    overflow: hidden;
    span{
      display: block;
      transform: translateY(80px);
      padding-left: 0.75em;
      animation: ttl-up 0.8s cubic-bezier(0.76, 0.09, 0.215, 1) forwards 0.4s;
    }
  }
  &__sub{
    letter-spacing: 0.25em;
    padding: 30px 0 30px 0;
    position: relative;
    font-size: 1.2rem;
    opacity: 0;
    animation: fade 0.2s linear forwards 0.8s;
  }
}
