.container {
  max-width: 1680px;
  margin-right: auto;
  margin-left: auto;
}

.p-title {
  height: 460px;
  text-align: center;
  position: relative;
  font-family: 'Barlow Condensed', sans-serif;
}

.p-title__txtarea {
  width: 100%;
  position: absolute;
  top: 50%;
}

.p-title__title {
  font-size: 7.2rem;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.75em;
  overflow: hidden;
}

.p-title__title span {
  display: block;
  transform: translateY(80px);
  padding-left: 0.75em;
  animation: ttl-up 0.8s cubic-bezier(0.76, 0.09, 0.215, 1) forwards 0.4s;
}

.p-title__sub {
  letter-spacing: 0.25em;
  padding: 30px 0 30px 0;
  position: relative;
  font-size: 1.2rem;
  opacity: 0;
  animation: fade 0.2s linear forwards 0.8s;
}

.p-title .scroll {
  width: 12px;
  height: 300px;
  position: absolute;
  top: 240px;
  right: 5.66%;
  transform: translateY(-45%);
}

.p-title .scroll p {
  position: absolute;
  top: 0;
  left: 12px;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  letter-spacing: 0.3em;
  transform: rotate(90deg);
  transform-origin: left top;
  line-height: 1;
  z-index: 9999;
}

.p-title .scroll__bar--1, .p-title .scroll__bar--2 {
  display: block;
  position: absolute;
  bottom: 0px;
  left: 6px;
  width: 1px;
  height: 230px;
  background: rgba(51, 51, 51, 0.1);
  pointer-events: none;
}

.p-title .scroll__bar--2 {
  background: rgba(51, 51, 51, 0.4);
  transform: scaleY(0);
  animation: scroll-bar 3s ease-in-out infinite forwards;
}

.back-imgarea--about {
  margin-bottom: 140px;
  height: 70vh;
}

.message {
  max-width: 1680px;
  margin: 0 auto;
  padding-bottom: 140px;
}

.message__container {
  padding-left: 33%;
}

.message__title {
  font-size: 3.4rem;
  font-weight: 500;
  letter-spacing: 0.3em;
  margin: 0 0 40px;
}

.message p {
  width: 740px;
  font-size: 1.7rem;
  margin-left: 20px;
  line-height: 2.6;
  margin-bottom: 1.2em;
}

.people {
  position: relative;
  padding-bottom: 100px;
}

.people::before {
  content: "";
  width: 100%;
  height: calc(100% - 300px);
  background: #f5f6f7;
  position: absolute;
  top: 300px;
  left: 0;
}

.people__container {
  display: flex;
  position: relative;
}

.people__titlearea {
  padding-top: 70px;
  padding-left: 6.6%;
  width: 33%;
  position: relative;
}

.people__title {
  position: relative;
  padding-bottom: 40px;
  padding-left: 5px;
  margin-bottom: 50px;
  display: block;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: 0.55em;
  font-size: 2.8rem;
  line-height: 1.5;
}

.people__title::after {
  content: "";
  width: 110%;
  height: 1px;
  background: #1c1f22;
  position: absolute;
  left: 0;
  bottom: 0;
}

.people__listarea {
  padding-right: 6.6%;
  width: 67%;
}

.people__list {
  display: flex;
  justify-content: space-between;
}

.people__item {
  width: 48%;
}

.people__img {
  overflow: hidden;
}

.people__txtarea {
  padding: 40px 0 50px;
  position: relative;
}

.people__txtarea::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 1px;
  background: #1c1f22;
  position: absolute;
  left: 0;
  bottom: 0;
}

.people__work {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 500;
  letter-spacing: 0.30em;
}

.people__name {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  font-size: 2.0rem;
  margin-right: 15px;
  margin-bottom: 15px;
}

.a-gallery {
  text-align: center;
  position: relative;
  padding-bottom: 140px;
}

.a-gallery::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 70%;
  background: #f5f6f7;
  position: absolute;
  left: 0;
  top: -5px;
  z-index: -1;
}

.a-gallery__title {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: 0.55em;
  font-size: 2.8rem;
  line-height: 1.5;
}

.a-gallery .gallery-slider {
  margin: 35px 0;
}

.a-gallery__item {
  width: 50vw;
  max-width: 780px;
  margin: 0 25px;
}

.outline {
  padding-bottom: 140px;
}

.outline__container {
  display: flex;
  justify-content: space-between;
}

.outline__titlearea {
  padding-top: 10px;
  padding-left: 6.6%;
  width: 33%;
}

.outline__title {
  width: 100%;
  font-size: 1.6rem;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: 0.55em;
  font-size: 2.8rem;
  line-height: 1.5;
}

.outline__tablearea {
  width: 67%;
  padding-right: 6.6%;
}

.outline__tablearea table {
  width: 100%;
}

.outline__tablearea table tr {
  border-top: 1px solid #d2d2d3;
  border-bottom: 1px solid #d2d2d3;
}

.outline__tablearea table tr td {
  padding: 25px 30px;
}

.outline__table-item {
  width: 18%;
  font-weight: 700;
  padding: 25px 30px;
}

.access {
  padding-bottom: 140px;
  position: relative;
}

.access__map {
  width: 100%;
  height: 590px;
  margin-bottom: 100px;
  overflow: hidden;
  position: relative;
}

.access__map img {
  height: 100%;
  width: auto;
}

.access__btn {
  position: absolute;
  right: 6.6%;
  top: 490px;
  margin-top: 50px;
}

.access__btn .link-btn {
  display: inline-block;
  width: 360px;
  line-height: 65px;
  text-align: right;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background: #1c1f22;
  color: #FFFFFF;
  letter-spacing: 0.2em;
  box-sizing: border-box;
  transition: .2s;
}

.access__btn .link-btn-line {
  display: block;
  width: 30px;
  height: 1px;
  background: #FFFFFF;
  position: absolute;
  right: 45px;
  top: 50%;
  transition: .2s;
}

.access__btn .link-btn::before {
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
}

.access__btn .link-btn::after {
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  right: 0;
  bottom: 0;
}

.access__btn .link-btn-bd {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding-right: 95px;
  transition: .2s;
}

.access__btn .link-btn-bd:hover {
  padding-right: 115px;
}

.access__btn .link-btn-bd::before {
  position: absolute;
  display: block;
  content: "";
  height: 0;
  width: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  left: 0;
  bottom: 0;
}

.access__btn .link-btn-bd::after {
  position: absolute;
  display: block;
  content: "";
  height: 0;
  width: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  top: 0;
  right: 0;
}

.access__btn .link-btn:hover {
  background: rgba(28, 31, 34, 0);
  color: #262626;
  opacity: 1;
}

.access__btn .link-btn:hover .link-btn-line {
  width: 50px;
  background: #1c1f22;
}

.access__btn .link-btn:hover::before {
  width: 100%;
  transition-delay: .1s;
}

.access__btn .link-btn:hover::after {
  width: 100%;
  transition-delay: .3s;
}

.access__btn .link-btn:hover .link-btn-bd::before {
  height: 100%;
  transition-delay: .4s;
}

.access__btn .link-btn:hover .link-btn-bd::after {
  height: 100%;
  transition-delay: .2s;
}

.access__container {
  max-width: 1080px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.access__txtarea {
  max-width: 440px;
}

.access__p-title {
  width: 100%;
  margin-bottom: 20px;
  font-size: 2.2rem;
}

.access__p-title span {
  display: block;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: 0.55em;
}

.access__exp {
  margin-top: 1em;
}

.access__img {
  margin-top: 55px;
}

.access__img--detail {
  max-width: 570px;
  margin-top: 10px;
}

.page-link ul {
  display: flex;
}

.page-link ul li {
  width: 33.3333%;
  text-align: center;
  height: 320px;
  overflow: hidden;
  position: relative;
}

.page-link ul li a {
  position: relative;
}

.page-link__img {
  position: relative;
  z-index: 8;
}

.page-link__mask {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: grey;
  opacity: .5;
  z-index: 9;
}

.page-link__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  z-index: 1000;
  line-height: 320px;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 2.2rem;
  letter-spacing: 0.75em;
  font-weight: 600;
  transition: .3s all;
  z-index: 10;
}

.page-link__title::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #ffffff;
  opacity: 1.0;
  transition: .3s all;
  z-index: -1;
}

.page-link__title:hover {
  color: white;
}

.page-link__title:hover::after {
  opacity: 0;
}
