@import '../mixin/link-btn';
@import '../mixin/page-title';
@import '../mixin/scroll-area';

$black-font:#262626;
$gley-font: #88909b;


.container{
  max-width: 1680px;
  margin-right: auto;
  margin-left: auto;
}



.p-title{
  @include _page-title;
  .scroll{
    @include _scroll-area(240px, 5.66%);
  }
}


.back-imgarea--about{
  margin-bottom: 140px;
  height: 70vh;
}

.message{
  max-width: 1680px;
  margin: 0 auto;
  padding-bottom: 140px;
  &__container{
    padding-left: 33%;
  }
  &__title{
    font-size: 3.4rem;
    font-weight: 500;
    letter-spacing: 0.3em;
    margin: 0 0 40px;
  }
  p{
    width: 740px;
    font-size: 1.7rem;
    margin-left: 20px;
    line-height: 2.6;
    margin-bottom: 1.2em;
  }
}


.people{
  position: relative;
  padding-bottom: 100px;
  &::before{
    content: "";
    width: 100%;
    height: calc(100% - 300px);
    background: #f5f6f7;
    position: absolute;
    top: 300px;
    left: 0;
  }
  &__container{
    display: flex;
    position: relative;
  }
  &__titlearea{
    padding-top: 70px;
    padding-left: 6.6%;
    width: 33%;
    position: relative;
  }
  &__title{
    position: relative;
    padding-bottom: 40px;
    padding-left: 5px;
    margin-bottom: 50px;
    display: block;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    letter-spacing: 0.55em;
    font-size: 2.8rem;
    line-height: 1.5;
    &::after{
      content: "";
      width: 110%;
      height: 1px;
      background: #1c1f22;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
  &__listarea{
    padding-right: 6.6%;
    width: 67%;
  }
  &__list{
    display: flex;
    justify-content: space-between;
  }
  &__item{
    width: 48%;
  }
  &__img{
    overflow: hidden;
  }
  &__txtarea{
    padding: 40px 0 50px;
    position: relative;
    &::after{
      position: absolute;
      content: "";
      width: 30px;
      height: 1px;
      background: #1c1f22;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
  &__work{
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 500;
    letter-spacing: 0.30em;
  }
  &__name{
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    font-size: 2.0rem;
    margin-right: 15px;
    margin-bottom: 15px;
  }
}


.a-gallery{
  text-align: center;
  position: relative;
  padding-bottom: 140px;
  &::before{
    position: absolute;
    content: "";
    width: 100%;
    height: 70%;
    background: #f5f6f7;
    position: absolute;
    left: 0;
    top: -5px;
    z-index: -1;
  }
  &__title{
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    letter-spacing: 0.55em;
    font-size: 2.8rem;
    line-height: 1.5;
  }
  .gallery-slider{
    margin: 35px 0;
  }
  &__item{
    width: 50vw;
    max-width: 780px;
    margin: 0 25px;
  }
}


.outline{
  padding-bottom: 140px;
  &__container{
    display: flex;
    justify-content: space-between;
  }
  &__titlearea{
    padding-top: 10px;
    padding-left: 6.6%;
    width: 33%;
  }
  &__title{
    width: 100%;
    font-size: 1.6rem;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    letter-spacing: 0.55em;
    font-size: 2.8rem;
    line-height: 1.5;
  }
  &__tablearea{
    width: 67%;
    padding-right: 6.6%;
    table{
      width: 100%;
      tr{
        border-top: 1px solid #d2d2d3;
        border-bottom: 1px solid #d2d2d3;
        td{
          padding: 25px 30px;
        }
      }      
    }
  }
  &__table-item{
    width: 18%;
    font-weight: 700;
    padding: 25px 30px;
  }
}


.access{
  padding-bottom: 140px;
  position: relative;
  &__map{
    width: 100%;
    height: 590px;
    margin-bottom: 100px;
    overflow: hidden;
    position: relative;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__btn{
    position: absolute;
    right: 6.6%;
    top: 490px;
    margin-top: 50px;
    .link-btn{
      @include _link-btn;
    }
  }
  &__container{
    max-width: 1080px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  &__txtarea{
    max-width: 440px;
  }
  &__p-title{
    width: 100%;
    margin-bottom: 20px;
    font-size: 2.2rem;
    span{
      display: block;
      font-family: 'Barlow Condensed', sans-serif;
      font-weight: 600;
      letter-spacing: 0.55em;
    }
  }
  &__exp{
    margin-top: 1em;
  }
  &__img{
    margin-top: 55px;
  }
  &__img--detail{
    max-width: 570px;
    margin-top: 10px;
  }
}

.page-link{
  ul{
    display: flex;
    li{
      width: 33.3333%;
      text-align: center;
      height: 320px;
      overflow: hidden;
      position: relative;
      a{
        position: relative;
      }
    }
  }
  &__img{
    position: relative;
    z-index: 8;
  }
  &__mask{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    background-color: grey;
    opacity: .5;
    z-index: 9;
  }
  &__title{
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    z-index: 1000;
    line-height: 320px;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 2.2rem;
    letter-spacing: 0.75em;
    font-weight: 600;
    transition: .3s all;
    z-index: 10;
    &::after{
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: #ffffff;
      opacity: 1.0;
      transition: .3s all;
      z-index: -1;
    }
    &:hover{
      color:white;
    }
    &:hover::after{
      opacity: 0;
    }
  }
}